.fold {
  right: 50px;
  margin-top: 5px;
  position: absolute;
}
.nav-max-height {
  max-height: 450px;
  overflow: scroll;
}
details summary:hover {
  background-color: #f8f9fa;
  font-weight: 700;
}
details summary {
  list-style-image: none;
  outline: none;
  position: relative;
}

summary::-webkit-details-marker {
  background: none;
  color: transparent;
}

details p {
  padding-left: 65px;
  cursor: pointer;
  font-size: 13px;
}
